<template>
  <div class="home">
    <!-- 头部导航 -->
    <HomeHeader :backgroundColor="headBg"></HomeHeader>
    <!-- 背景图片 -->
    <div class="bg">
      <div class="next" @click="goBottom">
        <img src="@/assets/icon/bottom.svg" alt="" />
      </div>
    </div>
    <!-- 文章列表 -->
    <div class="article">
      <div class="content">
        <ArticleList></ArticleList>
      </div>
    </div>
    <!--  -->
    <Footer></Footer>
  </div>
</template>

<script>
import appConfig from '@/config/app.json'

export default {
  name: "Home",
  data() {
    return {
      oldH: 0, // 滚动之前的位置
      headBg: "transparent",
      Author: appConfig.Author,
    };
  },
  mounted() {
    document.title = `${this.Author} - 个人博客`;
    window.addEventListener("mousewheel", this.scrollFunc);
    this.headBg = sessionStorage.getItem("headBg");
  },
  destroyed() {
    window.removeEventListener("mousewheel", this.scrollFunc);
  },
  methods: {
    // 滚动到下面
    goBottom() {
      window.scrollTo({
        top: document.documentElement.clientHeight - 40,
        behavior: "smooth",
      }); // 滚动到需要到位置
      setTimeout(() => {
        this.headBg = "#1f262f";
        sessionStorage.setItem("headBg", "#1f262f");
      }, 500);
    },
    // 滚动到顶部
    goTop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      }); // 滚动到需要到位置
      this.headBg = "transparent";
      sessionStorage.setItem("headBg", "transparent");
    },
    scrollFunc() {
      var e = e || window.event;
      if (e.wheelDelta) {
        this.scrollFunc1(-e.wheelDelta);
      } else if (e.detail) {
        this.scrollFunc1(-e.detail);
      }
    },
    // 滚动
    scrollFunc1(direction) {
      const viewHeight = document.documentElement.clientHeight; // 窗口高度
      const nowH = window.scrollY; // 此时距离顶部的高度
      if (nowH < viewHeight - 60 && direction > 0) {
        this.goBottom();
      }
      if (nowH <= viewHeight - 40 && direction < 0) {
        this.goTop();
      }
      this.oldH = window.scrollY;
    },
  },
};
</script>

<style lang="less" scoped>
.home {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  min-width: 1200px;
  .bg {
    position: relative;
    margin-top: -60px;
    width: 100%;
    height: 100vh;
    background: url(https://s1.ax1x.com/2022/09/13/vjd1v4.jpg) no-repeat 0 0 /
      100% 100%;
    @keyframes down {
      0% {
        opacity: 1;
        transform: translate(-50%, 0);
      }
      50% {
        opacity: 0.3;
        transform: translate(-50%, 20px);
      }
      100% {
        opacity: 1;
        transform: translate(-50%, 0px);
      }
    }
    .next {
      z-index: 5;
      position: absolute;
      padding: 20px;
      width: 40px;
      height: 40px;
      bottom: 30px;
      left: 50%;
      transform: translate(-50%, 0);
      animation: down 1.5s infinite;
      cursor: pointer;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .article {
    box-sizing: border-box;
    display: flex;
    padding: 20px 0;
    justify-content: center;
    min-height: calc(100vh - 60px);
    background-color: #eaeaea;
    .content {
      box-sizing: border-box;
      padding-top: 24px;
      width: 1200px;
    }
  }
}
</style>
